<template>
    <div class="navigation" data-enable="true">
        <!-- 助航选择 -->
        <div class="routes shadow aberration-wrapper-color">
            <div class="routes-back" @click="handlerBack">
                <mu-icon value="navigate_before" size="30"/>
            </div>

            <div class="point-group aberration-color">
                <i class="point sn"></i>
                <i class="point"></i>
                <i class="point"></i>
                <i class="point"></i>
                <i class="point en"></i>
            </div>

            <div class="route-group aberration-color">
                <div class="route sn no-wrap" @click="handlerRoute('route-sn')">
                        <span :class="{'theme-text-secondary': !sn.name}">
                        {{sn.name || sn.p}}
                    </span>
                </div>
                <div class="route en no-wrap" @click="handlerRoute('route-en')">
                        <span :class="{'theme-text-secondary': !en.name}">
                            {{en.name || en.p}}
                        </span>
                </div>
                <img class="route-reverse"
                     :src="require('../../../static/icon/navigation_line.png')"
                     @click="reverseRoutes">
            </div>
        </div>

        <!-- 助航路线的历史纪录 -->
        <transition name="views-down">
            <div class="routes-wrapper paper" v-if="visible">
                <routes @change="change"/>
            </div>
        </transition>

        <transition name="up-move">
            <!-- navigation panel start -->
            <div class="navigation-panel paper" v-if="routes.length > 0">
                <div class="flex border-1px-b">
                    <div class="item p-10 flex-1" v-for="(item, index) in routes"
                         :class="{'theme-selected': id === item.attributes.ObjectID}"
                         @click="handlerToggleNavigation(item)">
                        <div class="lhp tc fz-18">{{index === 0 ? '推荐航线' : '备选航线'}}</div>
                        <div class="flex jcc aic">
                            <div class="mr-10 lht tc fz-18">
                                {{item.attributes.Shape_Length | formatDistance}}<br/>
                                {{item.attributes.Shape_Length > 1000 ? '公里' : '米'}}
                            </div>
                            <div>
                                <p class="lhp fz-14">{{item.attributes.Bridge_Number}} 座桥梁</p>
                                <p class="lhp fz-14">{{item.attributes.ShipLock_Number}} 座船闸</p>
                                <p class="lhp fz-14">
                                    {{item.attributes.Total_TravelTime * 60 | secondToDate}}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="navigation-panel-button">
                    <mu-button class="button" @click="guide" round flat>模拟助航</mu-button>
                    <mu-button class="button" @click="start" round flat>开始助航</mu-button>
                </div>
            </div>
            <!-- navigation panel end -->
        </transition>

        <!-- 助航的路由 -->
        <transition name="views-left">
            <keep-alive>
                <router-view class="views"/>
            </keep-alive>
        </transition>
        <!-- 助航的路由 -->
    </div>
</template>

<script>
    import routes from './history'
    import {routeMixins, userMixins} from "../../common/mixins"
    import {getMapLocation} from "../../common/js/cordova/map"
    import {getNavigationRoute} from "../../api/navigation"
    import {debounce} from "../../types/index"
    import {secondToDate} from '../../common/utils/time'

    const KN = '节'
    const KM = 'km/h'

    export default {
        name: "navigation",
        mixins: [userMixins, routeMixins],
        data() {
            return {
                id: '',
                routes: [],
                ship: {
                    id: '',
                    speed: 0,
                    draft: 0,
                    pureHeight: 0,
                    unit: KN
                }
            }
        },
        computed: {
            visible() {
                return !(this.sn.name && this.en.name)
            }
        },
        created() {
            const plan = debounce(() => {
                this.planRoutes()
            }, 500)

            this.$watch('sail', plan, {deep: true})
        },
        activated() {
            if (JSON.stringify(this.$route.query) === '{}') {
                this.ship.id = ''
                this.ship.speed = 0
                this.ship.draft = 0
                this.ship.pureHeight = 0
                this.ship.unit = KM

                return
            }

            this.ship.id = this.$route.query.shipId
            this.ship.speed = this.$route.query.speed
            this.ship.draft = this.$route.query.draft
            this.ship.pureHeight = this.$route.query.pureHeight
            this.ship.unit = this.$route.query.unit

            if (!this.sail) {
                this.routes = []
            }
        },
        methods: {
            // 更改助航线路
            change(item) {
                this.setRouteSn(item.sn)
                this.setRouteEn(item.en)

                const route = JSON.stringify({
                    sn: item.sn,
                    en: item.en
                })

                this.saveRouteHistory(route)
            },
            // 点击返回
            handlerBack() {
                window.Arcgis && window.Arcgis.cancelPathPlanning('')

                this.resetRoutes()

                this.$router.back()
            },
            // 点击进入助航地点选择
            handlerRoute(name) {
                this.$router.push({name})
            },
            // 点击切换当前方案 &*
            handlerToggleNavigation(item) {
                this.$toast({
                    position: 'center',
                    type: 'loading',
                    message: '请等待 ...',
                    duration: 0
                })

                window.Arcgis && window.Arcgis.changeRoute(Number.parseInt(item.attributes.ObjectID), res => {
                    this.id = item.attributes.ObjectID

                    this.$toast.clear()

                    console.log('%c—————— 切换路线方案 ——————', 'color: #2196f3', item)
                })
            },
            // 点击开始助航
            start() {
                if (!this.isLogin) {
                    this.$toast({
                        type: 'fail',
                        position: 'center',
                        message: '您需要登录才能进行助航！'
                    })

                    return
                }

                this.$toast({
                    type: 'loading',
                    position: 'center',
                    message: '正在规划线路, 请等待 ...',
                    duration: 0
                })

                const route = JSON.stringify({
                    sn: this.sn,
                    en: this.en
                })

                this.saveRouteHistory(route)

                this.routes = []

                window.Arcgis && window.Arcgis.realGuide(this.id, res => {
                    try {
                        res = JSON.parse(res)
                    } catch (e) {
                        console.log(e)

                        res = {
                            speed: 0,
                            polar: 0,
                            distance: 0,
                            word: ''
                        }
                    }

                    this.$toast.clear()

                    this.$router.replace({
                        name: 'sail',
                        params: {
                            speed: res.speed,
                            polar: res.polar,
                            distance: res.distance,
                            word: res.word,
                            ship: this.ship
                        }
                    })
                }, err => {
                    this.$toast.clear()
                })
            },
            guide() {
                this.$toast({
                    type: 'loading',
                    position: 'center',
                    message: '正在规划线路, 请等待 ...',
                    duration: 0
                })

                const route = JSON.stringify({
                    sn: this.sn,
                    en: this.en
                })

                this.saveRouteHistory(route)

                this.routes = []

                window.Arcgis && window.Arcgis.navigationStarting(this.id, res => {
                    try {
                        res = JSON.parse(res)
                    } catch (e) {
                        console.log(e)

                        res = {
                            speed: 0,
                            polar: 0,
                            distance: 0,
                            word: ''
                        }
                    }

                    this.$toast.clear()

                    this.$router.replace({
                        name: 'sail',
                        params: {
                            speed: res.speed,
                            polar: res.polar,
                            distance: res.distance,
                            word: res.word,
                            ship: this.ship
                        }
                    })
                }, err => {
                    this.$toast.clear()
                })
            },
            // 路线规划
            async planRoutes() {
                let sn = this.sn, en = this.en, point

                // 如果没有添加任何线路，则不进行线路规划
                if (!sn.name || !en.name) return

                // 如果起始位置为 我的位置 ，获取 经度/longitude&纬度/latitude 信息
                if (sn.name === '我的位置') {
                    point = await getMapLocation()

                    sn = Object.assign({}, sn, {longitude: point.x, latitude: point.y})
                }

                // 如果结束位置为 我的位置 ，获取 经度/longitude&纬度/latitude 信息
                if (en.name === '我的位置') {
                    point = await getMapLocation()

                    en = Object.assign({}, en, {longitude: point.x, latitude: point.y})
                }

                console.log(`起始点位信息: `, sn)
                console.log(`结束点位信息: `, en)

                // 获取线路
                this.getRoutes(sn, en)
            },
            // 获取路线
            getRoutes(sn, en) {
                const data = {
                    startCoordinate: {
                        longitude: sn.longitude,
                        latitude: sn.latitude
                    },
                    goalCoordinate: {
                        longitude: en.longitude,
                        latitude: en.latitude
                    },
                    vehicle: {
                        vehicleID: "Passenger",
                        deadWeightTonnage: 0,
                        lengthOverall: 0,
                        mouldedBreadth: 0,
                        mouldedDepth: 0,
                        loadedDraft: this.ship.draft,
                        freeboardHeight: this.ship.pureHeight
                    },
                    averageSpeed: this.ship.unit === KM ? this.ship.speed : this.ship.speed * 1.852
                }

                console.log(data, '正在获取助航线路 ...')

                getNavigationRoute(data).then(res => {
                    if (!res) return

                    this.routes = res.routes.features

                    this.id = this.routes[0].attributes.ObjectID

                    window.Arcgis &&
                    window.Arcgis.pathPlanning(res, [sn, en], console.log)
                }).catch(console.log)
            }
        },
        filters: {
            secondToDate: secondToDate
        },
        components: {
            routes
        }
    }
</script>

<style lang="stylus">
    @import "../../common/stylus/mixins.styl"

    .navigation
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        background-color: transparent !important
        overflow hidden

        .routes
            position absolute
            left 0
            right 0
            top 0
            z-index 100
            display flex
            align-items center
            padding 10px 13px 10px
            height 110px
            opacity 0.9

            .routes-back
                margin-top 10px
                margin-right 10px
                align-self flex-start

            .point-group
                position relative
                display flex
                flex-direction column
                align-items center
                justify-content space-around
                width 38px
                height 80px
                border-radius 10px 0 0 10px

                .point
                    display block
                    width 0
                    height 0
                    border 2px solid #DDDDDD
                    border-radius 50%

                    &.sn
                        margin-top 15px
                        border 4px solid #0DC56E

                    &.en
                        margin-bottom 15px
                        border 4px solid #F03737

            .route-group
                flex 1
                padding-right 60px
                border-radius 0 10px 10px 0
                overflow hidden

                .route
                    position relative
                    width 100%
                    height 40px
                    line-height 40px
                    transition all 1s

                    &:first-child:before
                        bottom-line(#E8E8E8)

                    span
                        font-size 14px

                .route-reverse
                    position absolute
                    top 50%
                    right 30px
                    width 25px
                    height 25px
                    transform translate3d(0, -50%, 0)

        .routes-wrapper
            position absolute
            top 0
            left 0
            padding-top 110px
            width 100%
            height 100%

        .navigation-panel
            position absolute
            bottom 0
            left 0
            right 0
            z-index 100
            opacity 0.9

            .item
                border-right .5px solid #FAFAFA
                transition color .5s

                &:last-child
                    border-right none

            .navigation-panel-button
                padding 13px 0
                text-align center

                .button
                    padding 10px
                    color #fff
                    background-color #4287FF
                    font-size $font-size-ml
                    border-radius 100px

                    &:last-child
                        margin-left 15px

</style>



